  <template>
<v-dialog width="350px" persistent v-model="editDialog">
<v-btn fab accent slot="activator">
<v-icon>edit</v-icon>
</v-btn>
<v-card>
<v-container>
<v-layout row wrap>
<v-flex xs12>
<v-card-title></v-card-title>
</v-flex>
</v-layout>
<v-divider></v-divider>
<v-layout row wrap>
<v-flex xs12>
<v-card-text>
<v-text-field 
name="manual" 
label="Keadaan Arduino" 
id="manual" 
v-model="editTitle" ></v-text-field>
<v-text-field 
name="status" 
label="STATUS CONTROLS" 
id="status" 
v-model="editlevel" ></v-text-field>
</v-card-text>
</v-flex>
</v-layout>
<v-divider></v-divider>
<v-layout row wrap>
<v-flex xs12>
<v-card-actions>
<v-btn flat class="blue--text darken-1" @click="editDialog = false">Close</v-btn>
<v-btn flat class="blue--text darken-1" @click="onSaveChange">Save</v-btn>
</v-card-actions>
</v-flex>
</v-layout>
</v-container>
</v-card>
</v-dialog>
</template>

<script>
 export default {
   props: ['control'],
   data () {
     return {
       editDialog: false,
       editTitle: this.control.manual,
       editlevel: this.control.status
     }
   },
   methods: {
     onSaveChange () {
       if (this.editTitle.trim() === '' || this.editlevel.trim() === '') {
         return
       }
       this.editDialog = false
       this.$store.dispatch('updateDataControl', {
         id: this.control.id,
         manual: this.editTitle,
         status: this.editlevel
       })
     }
   }
 }
</script>



