<template>
<v-container>
<v-layout row v-if="error">
<v-flex xs12 sm6 offset-sm3>
<app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
</v-flex>
</v-layout>
<v-layout>
<v-flex xs12 md6 offset-sm3>
<v-card>
<v-card-text>
<v-container>
<v-toolbar class="blue" dark>
<v-toolbar-title>HIDROPONIK V3</v-toolbar-title>
</br>
 </v-toolbar>
<form @submit.prevent="onSignup">
<v-layout row>
<v-flex xs12>
<v-text-field name="email" label="Mail" id="email" v-model="email" type="email" required></v-text-field>
</v-flex>
</v-layout>
<v-layout row>
<v-flex xs12>
<v-text-field name="password" label="Password" id="password" v-model="password" type="password" required ></v-text-field>
</v-flex>
</v-layout>
<v-layout row>
<v-flex xs12>
<v-text-field name="confirmPassword" label="ConfirmPassword" id="confirmPassword" v-model="confirmPassword" type="password" :rules="[comparePasswords]" required></v-text-field>
</v-flex>
</v-layout>
<v-layout row>
<v-flex xs12>
<v-btn type="submit" >Register/SignUp</v-btn>
</v-flex>
</v-layout>
</form>
</v-container>
</v-card-text>
</v-card>
</v-flex>
</v-layout>
</v-container>
</template>
<script>
export default {
  data () {
    return {
      email: '',
      password: '',
      confirmPassword: ''
    }
  },
  computed: {
    comparePasswords () {
      return this.password !== this.confirmPassword ? 'Password Do not Match' : ''
    },
    user () {
      return this.$store.getters.user
    },
    error () {
      return this.$store.getters.error
    }
  },
  watch: {
    user (value) {
      if (value !== null && value !== undefined) {
        this.$router.push('/')
      }
    }
  },
  methods: {
    onSignup () {
      this.$store.dispatch('signUserUp', {email: this.email, password: this.password})
    },
    onDismissed () {
      this.$store.dispatch('clearError')
    }
  }
}
</script>



