import Vue from 'vue'
import Vuetify from 'vuetify'
import App from './App'
import router from './router'
import { store } from './store'
import DateFilter from './filters/date'
import * as firebase from 'firebase'
import AlertCmp from './components/Shared/Alert.vue'
import EditMonitor from './components/Monitor/Edit/EditMonitor.vue'
import EditMonitorDialog from './components/Monitor/Edit/EditMonitorDialog.vue'
import EditMonitorTime from './components/Monitor/Edit/EditMonitorTime.vue'
import EditUser from './components/Monitor/Edit/EditUser.vue'
import EditControl from './components/Monitor/Edit/EditControl.vue'
import CreateControl from './components/User/CreateUser.vue'
import CommitChart from './newChart.js'
import VueResource from 'vue-resource'

Vue.use(VueResource)
Vue.use(Vuetify)
Vue.filter('date', DateFilter)
Vue.component('app-alert', AlertCmp)
Vue.component('app-edit-monitor-details-dialog', EditMonitor)
Vue.component('app-edit-monitor-date-dialog', EditMonitorDialog)
Vue.component('app-edit-monitor-time-dialog', EditMonitorTime)
Vue.component('app-edit-monitor-user-dialog', EditUser)
Vue.component('app-edit-monitor-control-dialog', EditControl)
Vue.component('app-dialog-create-user', CreateControl)
Vue.component('commitChart', CommitChart)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App),
  created () {
    firebase.initializeApp({
      apiKey: 'AIzaSyCqvezkQ9aYiAs1quW1-g1ao4dIz6fW42Y',
      authDomain: 'hydroponicarduino.firebaseapp.com',
      databaseURL: 'https://hydroponicarduino.firebaseio.com',
      projectId: 'hydroponicarduino',
      storageBucket: 'hydroponicarduino.appspot.com'
    })
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('autoSignIn', user)
      }
    })
    this.$store.dispatch('loadhiropo')
    this.$store.dispatch('loaduser')
    this.$store.dispatch('loadControl')
  }
})



