  import Vue from 'vue'
  import Vuex from 'vuex'
  import firebase from 'firebase'

  Vue.use(Vuex)

  export const store = new Vuex.Store({
    state: {
      loadedControls: [
        {
          diffTime: '',
          manual: '',
          lastOn: new Date(),
          status: '',
          idarduino: ''
        }
      ],
      loadeduser: [
        {
          email: '',
          idarduino: '',
          level: ''
        },
        {
          email: '',
          idarduino: '',
          level: ''
        }
      ],
      loadedMonitors: [
        { ec: '',
          idarduino: '',
          ketinggianWadah: '',
          ph: '',
          pupukA: '',
          pupukB: '',
          statusPupuk: '',
          suhu: '',
          tanggal: new Date()
        },
        { ec: '',
          idarduino: '',
          ketinggianWadah: '',
          ph: '',
          pupukA: '',
          pupukB: '',
          statusPupuk: '',
          suhu: '',
          tanggal: new Date()
        }
      ],
      user: null,
      loading: false,
      error: null
    },
    mutations: {
      createControl (state, payload) {
        state.loadedControls = payload
      },
      setloadcontrol (state, payload) {
        state.loadedControls = payload
      },
      setloaduser (state, payload) {
        state.loadeduser = payload
      },
      setloadHidropo (state, payload) {
        state.loadedMonitors = payload
      },
      createMonitor (state, payload) {
        state.loadedMonitors.push(payload)
      },
      createUser (state, payload) {
        state.loadeduser.push(payload)
      },
      setUser (state, payload) {
        state.user = payload
      },
      updateControl (state, payload) {
        const control = state.loadedControls.find(control => {
          return control.id === payload.id
        })
        if (payload.manual) {
          control.manual = payload.manual
        }
        if (payload.level) {
          control.level = payload.level
        }
      },
      updateUser (state, payload) {
        const users = state.loadeduser.find(users => {
          return users.id === payload.id
        })
        if (payload.idarduino) {
          users.email = payload.email
        }
        if (payload.level) {
          users.level = payload.level
        }
      },
      updateMonitor (state, payload) {
        const monitor = state.loadedMonitors.find(monitor => {
          return monitor.id === payload.id
        })
        if (payload.idarduino) {
          monitor.idarduino = payload.idarduino
        }
        if (payload.statusPupuk) {
          monitor.statusPupuk = payload.statusPupuk
        }
        if (payload.tanggal) {
          monitor.tanggal = payload.tanggal
        }
      },
      setLoading (state, payload) {
        state.loading = payload
      },
      setError (state, payload) {
        state.error = payload
      },
      clearError (state, payload) {
        state.error = null
      }
    },
    actions: {
      loadControl ({commit}) {
        commit('setLoading', true)
        firebase.database().ref('kendali').once('value')
        .then((data) => {
          const control = []
          const obj = data.val()
          for (let key in obj) {
            control.push({
              id: key,
              diffTime: obj[key].diffTime,
              manual: obj[key].manual,
              lastOn: obj[key].lastOn,
              status: obj[key].status
            })
          }
          commit('setloadcontrol', control)
          commit('setLoading', false)
        })
        .catch((error) => {
          console.log(error)
          commit('setLoading', true)
        })
      },
      loaduser ({commit}) {
        commit('setLoading', true)
        firebase.database().ref('pengguna/').once('value')
        .then((data) => {
          const users = []
          const obj = data.val()
          for (let key in obj) {
            users.push({
              id: key,
              email: obj[key].email,
              idarduino: obj[key].idarduino,
              level: obj[key].level,
              creatorId: obj[key].creatorId
            })
          }
          commit('setloaduser', users)
          commit('setLoading', false)
        })
        .catch((error) => {
          console.log(error)
          commit('setLoading', true)
        })
      },
      loadhiropo ({commit}) {
        commit('setLoading', true)
        firebase.database().ref('pantau/').once('value')
        .then((data) => {
          const monitors = []
          const obj = data.val()
          for (let key in obj) {
            monitors.push({
              id: key,
              ec: obj[key].ec,
              idarduino: obj[key].idarduino,
              ketinggianWadah: obj[key].ketinggianWadah,
              ph: obj[key].ph,
              pupukA: obj[key].pupukA,
              pupukB: obj[key].pupukB,
              statusPupuk: obj[key].statusPupuk,
              suhu: obj[key].suhu,
              tanggal: obj[key].tanggal,
              creatorId: obj[key].creatorId
            })
          }
          commit('setloadHidropo', monitors)
          commit('setLoading', false)
        })
        .catch((error) => {
          console.log(error)
          commit('setLoading', true)
        })
      },
      createControl ({commit}, payload) {
        let id = payload.idarduino
        const control = {
          diffTime: payload.diffTime,
          lastOn: payload.lastOn,
          manual: payload.manual,
          status: payload.status
        }
        firebase.database().ref('kendali/' + id).set(control)
        .then((data) => {
          console.log(data)
          commit('createControl', control)
        }).catch((error) => {
          console.log(error)
        })
      },
      createUser ({commit, getters}, payload) {
        const users = {
          email: payload.email,
          idarduino: payload.idarduino,
          level: payload.level
        }
        firebase.database().ref('pengguna/').push(users)
          .then((data) => {
            const key = data.key
            // create database
            commit('createUsers', {
              ...users,
              id: key
            })
            return key
          })
      },
      createMonitor ({commit, getters}, payload) {
        const monitor = {
          ec: payload.ec,
          ketinggianWadah: payload.ketinggianWadah,
          ph: payload.ph,
          pupukA: payload.pupukA,
          pupukB: payload.pupukB,
          suhu: payload.suhu,
          creatorId: getters.user.id
        }
        firebase.database().ref('pantau/').push(monitor)
          .then((data) => {
            const key = data.key
            // create database
            commit('createMonitor', {
              ...monitor,
              id: key
            })
          })
      // firebase here
      },
      updateDataControl ({commit}, payload) {
        commit('setLoading', true)
        const updateObj = {}
        if (payload.idarduino) {
          updateObj.idarduino = payload.idarduino
        }
        if (payload.status) {
          updateObj.status = payload.status
        }
        firebase.database().ref('control').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('updateControl', payload)
        })
          .catch(error => {
            console.log(error)
            commit('setLoading', false)
          })
      },
      updateDataUser ({commit}, payload) {
        commit('setLoading', true)
        const updateObj = {}
        if (payload.idarduino) {
          updateObj.idarduino = payload.idarduino
        }
        if (payload.level) {
          updateObj.level = payload.level
        }
        if (payload.email) {
          updateObj.email = payload.email
        }
        firebase.database().ref('pengguna').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('updateUser', payload)
        })
          .catch(error => {
            console.log(error)
            commit('setLoading', false)
          })
      },
      updateDataMonitor ({commit}, payload) {
        commit('setLoading', true)
        const updateObj = {}
        if (payload.idarduino) {
          updateObj.idarduino = payload.idarduino
        }
        if (payload.statusPupuk) {
          updateObj.statusPupuk = payload.statusPupuk
        }
        if (payload.tanggal) {
          updateObj.tanggal = payload.tanggal
        }
        firebase.database().ref('monitor').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('updateMonitor', payload)
        })
          .catch(error => {
            console.log(error)
            commit('setLoading', false)
          })
      },
      signUserUp ({commit}, payload) {
        commit('setLoading', true)
        commit('setError')
        firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
          .then(
            user => {
              commit('setLoading', false)
              const newUser = {
                id: user.uid,
                registerMonitors: []
              }
              commit('setUser', newUser)
            }
          )
          .catch(
            error => {
              console.log(error)
              commit('setLoading', false)
              commit('setError', error)
            }
          )
      },
      signUserIn ({commit}, payload) {
        commit('setLoading', true)
        commit('setError')
        firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
        .then(
          user => {
            commit('setLoading', false)
            const newUser = {
              id: user.uid,
              registerMonitors: []
            }
            commit('setUser', newUser)
          }
        )
        .catch(
            error => {
              commit('setLoading', true)
              commit('setError')
              console.log(error)
            }
          )
      },
      autoSignIn ({commit}, payload) {
        commit('setUser', {id: payload.uid, registerMonitors: []})
      },
      logout ({commit}) {
        firebase.auth().signOut()
        commit('setUser', null)
      },
      clearError ({commit}) {
        commit('clearError')
      }
    },
    getters: {
      loadedControls (state) {
        return state.loadedControls.sort((controlA, controlB) => {
          return controlA.lastOn > controlB.lastOn
        })
      },
      loadeduser (state) {
        return state.loadeduser.sort((userA, userB) => {
          return userA.idarduino > userB.idarduino
        })
      },
      loadedMonitors (state) {
        return state.loadedMonitors.sort((monitorA, monitorB) => {
          return monitorA.tanggal > monitorB.tanggal
        })
      },
      fetureMonitor (state, getters) {
        return getters.loadedMonitors.slice(0.5)
      },
      fetureUser (state, getters) {
        return getters.loadeduser.slice(0.5)
      },
      usermonitor (state) {
        return (userId) => {
          return state.loadeduser.find((users) => {
            return users.id === userId
          })
        }
      },
      loadedControl (state) {
        return (controlId) => {
          return state.loadedControls.find((control) => {
            return control.id === controlId
          })
        }
      },
      loadedMonitor (state) {
        return (monitorId) => {
          return state.loadedMonitors.find((monitor) => {
            return monitor.id === monitorId
          })
        }
      },
      loadedMonitor1 (state) {
        return (tanggalId) => {
          return state.loadedMonitors.find((monitor) => {
            return monitor.tanggal === tanggalId
          })
        }
      },
      user (state) {
        return state.user
      },
      error (state) {
        return state.error
      },
      loading (state) {
        return state.loading
      }
    }
  })



