<template>
<v-container>
<v-layout row wrap class="mb-2">
<center>
      <div class="panel-body">
        <table class="table">
          <thead>
            <tr>
              <th >ID ARDINO</th>
            </tr>
          </thead>
          <tbody>
          <tr v-for="monitors in monitors">
              <td>
                {{ monitors.id }}
              </td>
              <td>
            <v-btn class="primary" :to="'/Monitor/' + monitors.id + '/' + monitors.id">View</v-btn>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
  </div>
  </div>
  </center>
  </v-layout>
  </v-container>
</template>
  
<script>
  export default {
    computed: {
      monitors () {
        return this.$store.getters.loadedMonitors
      },
      loading () {
        return this.$store.getters.loading
      }
    }
  }
</script>



