<template>
<v-container>
<div v-for="monitor in monitors">
<article>{{monitor.id}}</article>
</div>
</v-container>
</template>

<script>
export default {
  data () {
    return {
      monitors: []
    }
  },
  created () {
    this.$http.get('https://hydroponicarduino.firebaseio.com/pantau/.json').then(function (data) {
      return data.json()
    }).then(function (data) {
      var monitorArray = []
      for (let key in data) {
        data[key].id = key
        monitorArray.push(data[key])
        console.log(data[key])
      }
      this.monitors = monitorArray
      console.log(monitorArray)
    })
  }
}
</script>



