<template>
<v-container>
<v-layout row v-if="error">
<v-flex xs12 sm6 offset-sm3>
<app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
</v-flex>
</v-layout>
<v-layout>
<v-flex xs12 md6 offset-sm3>
<v-card>
<v-card-text>
<v-container>
<v-toolbar class="blue" dark>
<v-toolbar-title>HIDROPONIK V3</v-toolbar-title>
</br>
 </v-toolbar>
<form @submit.prevent="onSignin">
<v-layout row>
<v-flex xs12>
<v-text-field name="email" label="Mail" id="email" v-model="email" type="email" required></v-text-field>
</v-flex>
</v-layout>
<v-layout row>
<v-flex xs12>
<v-text-field name="password" label="Password" id="password" v-model="password" type="password" required ></v-text-field>
</v-flex>
</v-layout>
<v-layout row>
<v-flex xs12>
<v-btn type="submit">Login</v-btn>
<v-btn v-on:click="googleLogin()" class="primary">
  Google in
</v-btn>
</v-flex>
</v-layout>
</form>
</v-container>
</v-card-text>
</v-card>
</v-flex>
</v-layout>
</v-container>
</template>
<script>
import Firebase from 'firebase'
var provider = new Firebase.auth.GoogleAuthProvider()
export default {
  data () {
    return {
      email: '',
      password: ''
    }
  },
  computed: {
    user () {
      return this.$store.getters.user
    },
    error () {
      return this.$store.getters.error
    }
  },
  watch: {
    user (value) {
      if (value !== null && value !== undefined) {
        this.$router.push('/')
      }
    }
  },
  methods: {
    onSignin () {
      this.$store.dispatch('signUserIn', {email: this.email, password: this.password})
    },
    onDismissed () {
      this.$store.dispatch('clearError')
    },
    googleLogin: function () {
      Firebase.auth().signInWithPopup(provider).then(user => {
        this.$store.dispatch('setUser', user)
        this.$router.push('/form')
      })
      .catch((error) => {
        console.log(error)
      })
    }
  }
}
</script>



