import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Chart from '@/components/Chart'
import Weather from '@/components/Weather'
import Signup from '@/components/User/Signup'
import Signin from '@/components/User/Signin'
import Profile from '@/components/User/Profile'
import Profileid from '@/components/User/Profileid'
import CreateMonitor from '@/components/Monitor/CreateMonitor'
import CreateControl from '@/components/Monitor/CreateControl'
import Controlling from '@/components/Monitor/Controlling'
import Controls from '@/components/Monitor/Controls'
import Monitors from '@/components/Monitor/Monitors'
import Monitor from '@/components/Monitor/Monitor'
import Form from '@/components/Monitor/Form'
import AuthGuard from './auth-guard'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/signup',
      name: 'Signup',
      component: Signup
    },
    {
      path: '/signin',
      name: 'Signin',
      component: Signin
    },
    {
      path: '/profile/',
      name: 'Profile',
      component: Profile,
      beforeEnter: AuthGuard
    },
    {
      path: '/profileid/:id',
      name: 'Profileid',
      props: true,
      component: Profileid,
      beforeEnter: AuthGuard
    },
    {
      path: '/createmonitor',
      name: 'CreateMonitor',
      component: CreateMonitor,
      beforeEnter: AuthGuard
    },
    {
      path: '/monitor/:id/:id/',
      name: 'Monitor',
      props: true,
      component: Monitor,
      beforeEnter: AuthGuard
    },
    {
      path: '/monitor',
      name: 'Monitors',
      component: Monitors,
      beforeEnter: AuthGuard
    },
    {
      path: '/form',
      name: 'Form',
      component: Form,
      beforeEnter: AuthGuard
    },
    {
      path: '/chart',
      name: 'Chart',
      component: Chart,
      beforeEnter: AuthGuard
    },
    {
      path: '/control',
      name: 'Control',
      component: Controlling,
      beforeEnter: AuthGuard
    },
    {
      path: '/controls/:id',
      props: true,
      name: 'Controls',
      component: Controls
    },
    {
      path: '/weather/',
      name: 'Weather',
      component: Weather,
      beforeEnter: AuthGuard
    },
    {
      path: '/createControl/',
      name: 'CreateControl',
      component: CreateControl,
      beforeEnter: AuthGuard
    }
  ]
})



