  <template>
<v-dialog width="350px" persistent v-model="editDialog">
<v-btn fab accent slot="activator">
<v-icon>edit</v-icon>
</v-btn>
<v-card>
<v-container>
<v-layout row wrap>
<v-flex xs12>
<v-card-title>Edit Users</v-card-title>
</v-flex>
</v-layout>
<v-divider></v-divider>
<v-layout row wrap>
<v-flex xs12>
<v-card-text>
<v-text-field 
name="email" 
label="email" 
id="email" 
v-model="editEmail" ></v-text-field>
<v-text-field 
name="level" 
label="level" 
id="level" 
v-model="editlevel" ></v-text-field>
<v-text-field 
name="idarduino" 
label="idarduino" 
id="idarduino" 
v-model="editIdArduino" ></v-text-field>
</v-card-text>
</v-flex>
</v-layout>
<v-divider></v-divider>
<v-layout row wrap>
<v-flex xs12>
<v-card-actions>
<v-btn flat class="blue--text darken-1" @click="editDialog = false">Close</v-btn>
<v-btn flat class="blue--text darken-1" @click="onSaveChange">Save</v-btn>
</v-card-actions>
</v-flex>
</v-layout>
</v-container>
</v-card>
</v-dialog>
</template>

<script>
 export default {
   props: ['users'],
   data () {
     let list = []
     // console.log(this.users.idarduino.indexOf(','))
     // console.log(this.users.idarduino)
     if (this.users.idarduino.indexOf(',') === -1) {
       list = this.users.idarduino
     } else {
       list = this.users.idarduino.split(',')
     }
     this.users.idarduino = list
     return {
       editDialog: false,
       editlevel: this.users.level,
       editEmail: this.users.email,
       editIdArduino: this.users.idarduino
     }
   },
   methods: {
     onSaveChange () {
       if (this.editlevel.trim() === '' || this.editEmail.trim() === '') {
         return
       }
       this.editDialog = false
       let list = []
       if (this.editIdArduino.indexOf(',') === -1) {
         list = this.editIdArduino
       } else {
         list = this.editIdArduino.split(',')
       }
       this.editIdArduino = list
       // biar bisa render array
       Object.assign(this.users.idarduino, { 0: list[0] })
       Object.assign(this.users.idarduino, { 1: list[1] })
       Object.assign(this.users.idarduino, { 2: 'ke njmvhmvmvmvnmvnmv' })
       // Object.assign(this.users.idarduino, { 3: 'list[3]' })
       for (let i = 0; i < list.length; i++) {
         console.log('ke ' + i + ' isinnya ' + list[i])
         // Object.assign(this.users.idarduino, { i: list[i] })
       }
       this.$store.dispatch('updateDataUser', {
         id: this.users.id,
         level: this.editlevel,
         email: this.editEmail,
         idarduino: this.idarduino
        // harusnya disimpan ['dsdsd', 'cccccc'],sama [{'0': 'dsdsd'}, {'1':'cccccc'}] , bukan 'dsdsd, cccccc'
       })
     }
   }
 }
</script>



