<template>
<v-container>
<v-layout row wrap v-if="isloading">
    <v-flex xs12 class="text-xs-center">
    <v-progress-circular indeterminate class="primary--text" :width="7" :size="70"> 
    </v-progress-circular>
    </v-flex>
    </v-layout>
<v-layout row wrap v-else>
<v-flex>
<v-card>
<v-card-title>
<h6 class="primary--text">{{ users.level }}</h6>
<v-spacer></v-spacer>
<h6 class="primary--text">{{ users.email }}</h6>
<template v-if="true">
<v-spacer></v-spacer>
</template>
</v-card-title>
</v-card>   
<v-card-text>
<div> {{ users.level }} - {{ users.email }} - {{ users.idarduino}} </div>
</v-card-text>
<v-card-actions>
<v-spacer></v-spacer>
<v-btn large router to="/Profile" class="info">Back</v-btn>
<app-edit-monitor-user-dialog :users="users"></app-edit-monitor-user-dialog>
</v-card-actions>
</v-flex>
</v-layout>
</v-container>
</template>

<script>
export default {
  props: ['id'],
  computed: {
    users () {
      return this.$store.getters.usermonitor(this.id)
    },
    isloading () {
      return this.$store.getters.loading
    }
  }
}
</script>



