<template>
<v-container>
<v-layout row wrap v-if="isloading">
    <v-flex xs12 class="text-xs-center">
    <v-progress-circular indeterminate class="primary--text" :width="7" :size="70"> 
    </v-progress-circular>
    </v-flex>
    </v-layout>
<v-layout row wrap v-else>
<v-flex>
<v-card>
<v-card-title>
<h6 class="primary--text">{{ control.id }}</h6>
<v-spacer></v-spacer>
<h6 class="primary--text">{{ control.status }}</h6>
<template v-if="true">
<v-spacer></v-spacer>
<app-edit-monitor-control-dialog :control="control"></app-edit-monitor-control-dialog>
</template>
</v-card-title>
</v-card>
<v-card-text>
</v-card-text>
<v-card-actions>
<v-spacer></v-spacer>
<v-btn large router to="/control" class="info">Back</v-btn>
</v-card-actions>
</v-flex>
</v-layout>
</v-container>
</template>

<script>
export default {
  props: ['id'],
  computed: {
    control () {
      return this.$store.getters.loadedControl(this.id)
    },
    controls () {
      return this.$store.getters.loadedControls
    },
    isloading () {
      return this.$store.getters.loading
    }
  }
}
</script>



