<template>
  <v-app id="example-2" toolbar>
    <v-navigation-drawer absolute persistent light :mini-variant.sync="mini" v-model="drawer" overflow>
      <v-toolbar flat class="transparent">
        <v-list class="pa-0">
          <v-list-tile avatar>
            <v-list-tile-avatar>
              <img src="https://randomuser.me/api/portraits/men/85.jpg" />
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title>Admin</v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <v-btn icon @click.native.stop="mini = !mini">
                <v-icon>chevron_left</v-icon>
              </v-btn>
            </v-list-tile-action>
            <v-toolbar-items class="hidden-xs-only">
            <v-btn flat v-if="userIsAuthenticated" @click="onLogout">
            <v-icon right dark >exit_to_app</v-icon></v-btn>
    </v-toolbar-items>	

          </v-list-tile>
        </v-list>
      </v-toolbar>
      <v-list class="pt-0" dense>
        <v-divider></v-divider>
        <v-list-tile v-for="item in menuItems" :key="item.title" :to="item.link">
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>{{ item.title }}</v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar fixed class="indigo darken-4" dark>
      <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title>Toolbar</v-toolbar-title>
    </v-toolbar>
    <main>
    <v-toolbar-items class="hidden-xs-only">
            <v-btn flat v-for="item in menuItems" 
            :key="item.title"
            :to="item.link">
            <v-icon left dark >{{ item.icon }}</v-icon>{{ item.title }}</v-btn>
    </v-toolbar-items>
    <router-view></router-view>
    </main>
  </v-app>
</template>
<script>
  export default {
    data () {
      return {
        sideNav: false,
        drawer: true,
        mini: false,
        right: null
      }
    },
    computed: {
      menuItems () {
        let menuItems = [
          { icon: 'person', title: 'Sign up', link: '/signup' },
          { icon: 'lock_open', title: 'Sign in', link: '/signin' }
        ]
        if (this.userIsAuthenticated) {
          menuItems = [
          { icon: 'home', title: 'Home', link: '/' },
          { icon: 'info', title: 'Chart', link: '/chart' },
          { icon: 'person', title: 'Profile', link: '/profile' },
          { icon: 'info', title: 'Monitor', link: '/form' },
          { icon: 'info', title: 'control', link: '/control' },
          { icon: 'create', title: 'createMonitor', link: '/createmonitor' },
          { icon: 'create', title: 'createcontrol', link: '/createcontrol' }
          ]
        }
        return menuItems
      },
      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      }
    },
    methods: {
      onLogout () {
        this.$store.dispatch('logout')
      }
    }
  }
</script>

<style lang="stylus">
  @import './stylus/main'
</style>



