<template>
<v-container>
      <div class="panel-body">
        <table class="table table-striped">
          <thead>
            <tr>
            <th>NO USER</th>
              <th >ID ARDINO</th>
              <th>EMAIL USER</th>
              <th>HAK AKSES USER</th>
            </tr>
          </thead>
          <tbody>
          <tr v-for="controls in control">
          <td>
          {{ controls.id}}
          </td>
              <td>
                {{ controls.diffTime }}
              </td>
              <td>
                {{ controls.lastOn }}
              </td>
              <td>
            <v-btn class="primary" :to="'/controls/' + controls.id">View</v-btn>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="text-xs-center">
    <v-pagination :length="3" v-model="page" circle></v-pagination>
  </div>
      </div>
  </div>
  </div>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      page: 1
    }
  },
  computed: {
    control () {
      return this.$store.getters.loadedControls
    }
  }
}
</script>



