<template>
  <v-layout row justify-center>
    <v-dialog v-model="dialog" persistent width="40%" >
      <v-btn dark slot="activator">Create User</v-btn>
      <v-card>
        <v-card-title>
          <span class="headline">User Profile</span>
        </v-card-title>
        <v-card-text>
          <v-container grid-list-md>
            <v-layout wrap>
              <v-flex xs12>
                <v-text-field id="email" label="Email" name="email" v-model="email" required></v-text-field>
              </v-flex>
              <v-flex xs12 sm6>
                <v-text-field id="idarduino" label="ID Arduino" name="idarduino" v-model="idarduino" required></v-text-field>
              </v-flex>
              <v-flex xs12 sm6>
                <v-select
                id="level"
                name="level"
                  label="Level User"
                  required
                  :items="['User','Admin']"
                  v-model="level"
                ></v-select>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="blue--text darken-1" flat @click.native="dialog = false">Close</v-btn>
          <v-btn class="blue--text darken-1" :disabled="!formIsValid" flat @click.native="onCreateUser">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-layout>
</template>
<script>
export default {
  data () {
    return {
      dialog: false,
      email: '',
      idarduino: '',
      level: ''
    }
  },
  computed: {
    formIsValid () {
      return this.email !== '' &&
      this.idarduino !== '' &&
      this.level !== ''
    }
  },
  methods: {
    onCreateUser () {
      if (!this.formIsValid) {
        return
      }
      const userData = {
        email: this.email,
        idarduino: this.idarduino,
        level: this.level
      }
      this.$store.dispatch('createUser', userData)
      this.$router.push('/pengguna')
    }
  }
}
</script>



