<template>
<v-container>
<v-layout row wrap v-if="isloading">
    <v-flex xs12 class="text-xs-center">
    <v-progress-circular indeterminate class="primary--text" :width="7" :size="70"> 
    </v-progress-circular>
    </v-flex>
    </v-layout>
<v-layout row wrap v-else>
<v-flex>
<v-card>
<v-card-title>
<h6 class="primary--text">{{ monitor.id }}</h6>
<template v-if="true">
<v-spacer></v-spacer>
<app-edit-monitor-details-dialog :monitor="monitor"></app-edit-monitor-details-dialog>

</template>
</v-card-title>
</v-card>
<v-card-text>
<v-table>
<div> {{ monitor.idarduino }} - {{ monitor.tanggal | tanggal }}  </div>
</v-table>
</v-card-text>
<v-card-actions>
<v-spacer></v-spacer>
<v-btn large router to="/Monitor" class="info">Back</v-btn>
<div><app-edit-monitor-date-dialog :monitor="monitor"></app-edit-monitor-date-dialog></div>
<div><app-edit-monitor-time-dialog :monitor="monitor"></app-edit-monitor-time-dialog></div>
</v-card-actions>
</v-flex>
</v-layout>
</v-container>
</template>

<script>
export default {
  props: ['id'],
  computed: {
    monitor () {
      return this.$store.getters.loadedMonitor(this.id)
    },
    isloading () {
      return this.$store.getters.loading
    },
    userIsAuthenticated () {
      return this.$store.getters.user !== null && this.$store.getters.user !== undefined
    },
    userIsCreator () {
      if (!this.userIsAuthenticated) {
        return false
      }
      return this.$store.getters.user.id === this.monitor.creatorId
    }
  }
}
</script>



