<template>
<v-container>
<v-toolbar light>
    <v-toolbar-title>Add User</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items class="hidden-sm-and-down">
      <app-dialog-create-user :users="users"></app-dialog-create-user>
    </v-toolbar-items>
  </v-toolbar>
      <div class="panel-body">
        <table class="table table-striped">
          <thead>
            <tr>
            <th>NO USER</th>
              <th >ID ARDINO</th>
              <th>EMAIL USER</th>
              <th>HAK AKSES USER</th>
            </tr>
          </thead>
          <tbody>
          <tr v-for="monitors in users">
          <td>
          {{ monitors.id}}
          </td>
              <td>
                {{ monitors.idarduino }}
              </td>
              <td>
                {{ monitors.email }}
              </td>
               <td>
                {{ monitors.level }}
              </td>
              <td>
            <v-btn class="primary" :to="'/Profileid/' + monitors.id">View</v-btn>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
  </div>
  </div>
  </v-container>
</template>

<script>
  export default {
    computed: {
      users () {
        return this.$store.getters.loadeduser
      },
      loading () {
        return this.$store.getters.loading
      },
      items () {
        let items = [
          { text: 'face', title: 'Sign up', link: '/signup' },
          { text: 'lock_open', title: 'Sign in', link: '/signin' }
        ]
        return items
      }
    }
  }
</script>



